import React from "react";
import {
  Container,
  Row,
  Col,
  Card,
  FormRadio,
  ListGroup,
  ListGroupItem,
  Form,
  FormTextarea,
  FormInput
} from "shards-react";
import PageTitle from "../components/common/PageTitle";
import {
  StringToMorse as fn,
  StringToMorseCode as code,
  IMC_LIST
} from "../utils/stringToMorse";
import Layout from "../components/layout/MainLayout";
import ListMap from "../components/common/ListMap";

class StringToMorseConvertor extends React.Component {
  IMC = IMC_LIST();
  state = {
    input: "",
    output: ""
  };

  handleTextInputChange = e => {
    this.setState({
      input: e.target.value,
      output: fn(e.target.value)
    });
  };
  render() {
    const { input, output } = this.state;
    return (
      <Layout>
        <Container fluid className="main-content-container px-4">
          <Row noGutters className="page-header py-4">
            <PageTitle
              title="String To Morse Code Convertor"
              subtitle="Morse encoder"
              md="12"
              className="ml-sm-auto mr-sm-auto"
            />
          </Row>
          <Row>
            <Col lg="12">
              <Card small className="mb-4">
                <ListGroup flush>
                  <ListGroupItem className="p-3">
                    <Row>
                      <Col>
                        <Form>
                          <Row form>
                            {/* Description */}
                            <Col md="6" className="form-group">
                              <label htmlFor="feDescription">String</label>
                              <FormTextarea
                                id="feDescription"
                                placeholder="Save the world! "
                                rows="5"
                                onChange={this.handleTextInputChange}
                                value={input}
                              />
                            </Col>
                            <Col md="6" className="form-group">
                              <label htmlFor="feDescription">Morse Code</label>
                              <FormTextarea
                                id="feDescription"
                                placeholder="Your text here... ?"
                                rows="5"
                                value={output}
                                onChange={() => {}}
                              />
                            </Col>
                          </Row>
                        </Form>
                      </Col>
                    </Row>
                  </ListGroupItem>
                  <ListGroupItem className="p-4">
                    <strong className="text-muted d-block mb-2">
                      How it Works
                    </strong>
                    <pre className="code-part">
                      <code className="javascript p-3">{code}</code>
                      {/* <button
                      theme="white"
                      className="copy-button"
                      data-clipboard-text={convertohexCode}
                    >
                      <i className="material-icons">file_copy</i>
                   </button>*/}
                    </pre>
                  </ListGroupItem>
                  <ListGroupItem>
                    <Row>
                      <Col lg="4">
                        <ListMap
                          title="International Morse code"
                          list={this.IMC.slice(
                            0,
                            Math.ceil(this.IMC.length / 3)
                          )}
                        />
                      </Col>
                    </Row>
                  </ListGroupItem>
                  <ListGroupItem className="p-4">
                    <strong className="text-muted d-block mb-2">
                      About Unicode
                    </strong>
                    <p>
                      Unicode serves as a standard for computers to display
                      character data from any language. There are many
                      variations of the Unicode standard, including UTF-7,
                      UTF-8, UTF-16, UCS-2, as well as Big Endian / Little
                      Endian byte order variations.
                    </p>
                    <p>
                      Traditionally, the ASCII standard used 1 byte to store 1
                      character, resulting in 256 combinations. Unlike the ASCII
                      standard, Unicode character data utilizes multiple bytes
                      per character. This increases the number of possible
                      characters, and this may vary depending on what variation
                      of the Unicode standard is in use.
                    </p>
                    <p>
                      Unicode now replaces ASCII, ISO 8859 and EUC at all
                      levels. It enables users to handle not only practically
                      any script and language used on this planet, it also
                      supports a comprehensive set of mathematical and technical
                      symbols to simplify scientific information exchange.
                      <a href="https://www.cl.cam.ac.uk/~mgk25/unicode.html">
                        UTF-8 and Unicode FAQ for Unix/Linux
                      </a>
                    </p>
                  </ListGroupItem>
                </ListGroup>
              </Card>
            </Col>
          </Row>
        </Container>
      </Layout>
    );
  }
}

export default StringToMorseConvertor;
